import { Button, Form, Input, message, Modal } from 'antd'
import React, { useState } from 'react';
import { LockOutlined, UserOutlined } from '@ant-design/icons';
import { login } from '../utils'

function Login({ onSuccess }) {
  // state to display or not display pop up window of username password input   
  const [displayModal, setDisplayModal] = useState(false)

  // click to close login pop up window
  const handleCancel = () => {
    setDisplayModal(false)
  }

  // click to pop up login window
  const signinOnClick = () => {
    setDisplayModal(true)
  }

  // user click submit trigger onFinish, user input on <Form> in render() will transfer to data obj as an input in this function 
  const onFinish = (data) => {
    // call login function in utils.js, input is user's credentials data typed by user
    login(data)
      .then((data) => {
        setDisplayModal(false)
        // message is from antd library
        message.success(`Welcome back, ${data.name}`)
        // App.js's PageHeader.js will have <Login onSuccess={this.signinOnSuccess} />
        // which App will provide callback function signinOnSuccess here
        // will make App's loggedIn become true
        onSuccess()
      }).catch((err) => {
        message.error(err.message)
      })
  }

  // need to wrap multi jsx(as return only allow one jsx)
  // so we use <></> to wrap all jsx
  return (
    <>
      {
          // when user click "Login" button, will triger function signinOnClick to pop up sign in window
      }
      <Button shape="round" onClick={signinOnClick} style={{ marginRight: '20px' }}>
        Login
      </Button>
      {
            // From Ant Design: Modal dialogs (window).
            // title ..... are the properties of this Modal
      }
      <Modal
        title="Log in"
        // whether display this modal
        visible={displayModal}
        onCancel={handleCancel}
        footer={null}
        // click x on modal
        destroyOnClose={true}
      >
        {
            // From Ant Design: High performance Form component with data scope management. Including data collection, verification, and styles.
            // name ..... are the properties of this Form
        }
        <Form
          name="normal_login"
          onFinish={onFinish}
          preserve={false}
        >
          {
            // Form.Item is each part of Form 
            // name ..... are the properties of this Form.Item
          }
          <Form.Item
            name="user_id"
            rules={[{ required: true, message: 'Please input your Username!' }]}
          >
            {
                  // From ant design: Input A basic widget for getting the user input is a text field. 
                  // Keyboard and mouse can be used for providing or changing data.
                  // prefix is use to place an icon, UserOutlined is an user icon from antd 
                  // placeholder is background text
            }
            <Input prefix={<UserOutlined />} placeholder="Username" />
          </Form.Item>
          <Form.Item
            name="password"
            rules={[{ required: true, message: 'Please input your Password!' }]}
          >
            <Input.Password
              prefix={<LockOutlined />}
              placeholder="Password"
            />
          </Form.Item>
          <Form.Item>
            {
                // type is button type
                // From ant design: we recommend native <Button htmlType="submit" /> to submit a form.
            }
            <Button type="primary" htmlType="submit">
              Login
            </Button>
          </Form.Item>
        </Form>
      </Modal>
    </>
  )
}

// in js code, we need to export function before others can import this function
export default Login