import { Layout, Row, Col, Button } from 'antd'
import Favorites from './Favorites'
import Register from './Register'
import Login from './Login'
import React from 'react'

const { Header } = Layout

// top section of web, Header with Login, Register and Favorite buttons 
function PageHeader({ loggedIn, signoutOnClick, signinOnSuccess, favoriteItems }) {
  return (
    <Header>
      {
        // Login, Register and Favorite buttons in one row
      }
      <Row justify='space-between'>
        {
          // if log in, show Favorites component
        }
        <Col>
          {loggedIn && <Favorites favoriteItems={favoriteItems} />}
        </Col>
        {
          // if log in, show Logout button
        }
        <Col>
          {loggedIn && <Button shape="round" onClick={signoutOnClick}>Logout</Button>}
          {
            // if not log in, show Login and Register Conponent
          }
          {!loggedIn && (
            <>
              <Login onSuccess={signinOnSuccess} />
              <Register />
            </>
          )}
        </Col>
      </Row>
    </Header>
  )
}

export default PageHeader