import { Layout, Menu, message } from 'antd';
import React, { useEffect, useState } from 'react';
import {
  logout,
  getTopGames,
  getRecommendations,
  searchGameById,
  getFavoriteItem
} from './utils';
import CustomSearch from './components/CustomSearch';
import { LikeOutlined, FireOutlined } from '@ant-design/icons';
import PageHeader from './components/PageHeader';
import Home from './components/Home';

// get library from ant design
const { Sider, Content } = Layout

// App component represent the whole page
function App() {
  // whether log in
  const [loggedIn, setLoggedIn] = useState(false)
  // store top games for siders
  const [topGames, setTopGames] = useState([])
  // resources from backend
  // click Sider will trigger onGameSelect, 
  // which will call getRecommendations to get resources from backend
  const [resources, setResources] = useState({
    VIDEO: [],
    STREAM: [],
    CLIP: [],
  });

  // favorite items of loged in user
  const [favoriteItems, setFavoriteItems] = useState({
    VIDEO: [],
    STREAM: [],
    CLIP: [],
  });

  // once user login successfully, change login state to true and update resources from backend
  const signinOnSuccess = () => {
    getFavoriteItem().then((data) => {
      setLoggedIn(true);
      setFavoriteItems(data);
    });
  }

  // once user log out successfully, change login state to false and message client
  const signoutOnClick = () => {
    logout().then(() => {
      setLoggedIn(false)
      message.success('Successfully Signed out')
    }).catch((err) => {
      message.error(err.message)
    })
  }

  // once click a game in top games or Recommendations for you, call onGameSelect fn
  // input is game id or "Recommendation"
  const onGameSelect = ({ key }) => {
    if (key === "Recommendation") {
      // will set resources per the data from backend(recommendations for this user), see getRecommendations in utils.js
      getRecommendations().then((data) => {
        setResources(data);
      });

      return;
    }

    // will set resources per the data from backend(per game id), see searchGameById in utils.js
    searchGameById(key).then((data) => {
      setResources(data);
    });
  };
  
  // once click search in custom serach, update resources from data from backend
  const customSearchOnSuccess = (data) => {
    setResources(data);
  }

  // once user set or unset a favorite item, the favorite item list will change
  // this funciton is use to get new favorite item list from backend and update in front end
  const favoriteOnChange = () => {
    // see utils.js
    // get favorite items from backend for this user, and .then update these favorite items to state of App(favoriteItems)
    getFavoriteItem()
      .then((data) => {
        setFavoriteItems(data);
      })
      .catch((err) => {
        message.error(err.message);
      });
  };

  // array of "Recommendation" button, and top games menu
  const mapTopGamesToProps = (topGames) => [
    {
      // "Recommendation" button
      label: "Recommend for you!",
      key: "Recommendation",
      icon: <LikeOutlined />,
    },
    {
      // top games menu
      label: "Popular Games",
      key: "popular_games",
      icon: <FireOutlined />,
      // parse topGames array, 
      // and for each game in topGames array, give a function (the way how to show each game)
      children: topGames.map((game) => ({
        // lable is text to display
        label: game.name,
        key: game.id,
        // icon is mini image
        icon:
          <img
            alt="placeholder"
            src={game.box_art_url.replace('{height}', '40').replace('{width}', '40')}
            style={{ borderRadius: '50%', marginRight: '20px' }}
          />
      }))
    }
  ]

  // when first load top games, get top games and update topGames state of this App
  useEffect(() => {
    getTopGames()
      .then((data) => {
        setTopGames(data)
      }).catch((err) => {
        message.error(err.message)
      })
  }, [])

  return (
    <Layout>
      {
        // top section of web, Header with Login, Register and Favorite buttons 
      }
      <PageHeader
        // provide status of App to PageHeader
        loggedIn={loggedIn}
        signoutOnClick={signoutOnClick}
        signinOnSuccess={signinOnSuccess}
        favoriteItems={favoriteItems}
      />
      {
        // bottom section of web, can be split to Sider(left side) and another Layout(right side)
      }
      <Layout>
        {
          // left side
        }
        <Sider width={300} className="site-layout-background">
          {
            // CustomSearch component, Apps provide customSearchOnSuccess fn to CustomSearch component
          }
          <CustomSearch onSuccess={customSearchOnSuccess} />
          {
            // menu to show top games
          }
          <Menu
            mode="inline"
            // once click a game in top games or Recommendations for you, call onGameSelect fn
            onSelect={onGameSelect}
            style={{ marginTop: '10px' }}
            // display what items under this menu, call mapTopGamesToProps fn
            items={mapTopGamesToProps(topGames)}
          />
        </Sider>
        {
          // display right side
        }
        <Layout style={{ padding: '24px' }}>
          {
            // Content in ant design, which wrap Home component
          }
          <Content
            className="site-layout-background"
            style={{
              padding: 24,
              margin: 0,
              height: 800,
              overflow: 'auto'
            }}
          >
            {
              // App provide states to Home component(show all the videos, streams, clips on the right side)
            }
            <Home
              resources={resources}
              loggedIn={loggedIn}
              favoriteOnChange={favoriteOnChange}
              favoriteItems={favoriteItems}
            />
          </Content>
        </Layout>
      </Layout>
    </Layout>
  );
}

export default App;
