import React, { useState } from 'react'
import MenuItem from './MenuItem';
import { Menu, Button, Drawer } from 'antd';
import { EyeOutlined, YoutubeOutlined, VideoCameraOutlined, StarFilled } from '@ant-design/icons';

// get library from ant design
const { SubMenu } = Menu

// Favorite component, include the My Favorites button and right side drawer to pop
function Favorites({ favoriteItems }) {
  // whether display right side drawer for favorite items to pop up
  const [displayDrawer, setDisplayDrawer] = useState(false)
  // parse favoriteItems from PageHeaders from App to three arrays
  const { VIDEO, STREAM, CLIP } = favoriteItems;

  // close display drawer
  const onDrawerClose = () => {
    setDisplayDrawer(false)
  }

  // open display drawer
  const onFavoriteClick = () => {
    setDisplayDrawer(true)
  }

  return (
    <>
      {
        // include My Favorites button and Drawer on the right side to pop
      }
      {
        // once click My Favorites, will trigger onFavoriteClick fn
      }
      <Button type="primary" shape="round" onClick={onFavoriteClick} icon={<StarFilled />}>
        My Favorites
      </Button>
      {
        // drawer on the right side
      }
      <Drawer
        title="My Favorites"
        placement="right"
        width={720}
        visible={displayDrawer}
        onClose={onDrawerClose}
      >
        {
          // drawer include a Menu
        }
        <Menu
          mode="inline"
          defaultOpenKeys={['streams']}
          style={{ height: '100%', borderRight: 0 }}
          selectable={false}
        >
          {
            // drawer's menu include three subMenu from three item types
          }
          <SubMenu key={'streams'} icon={<EyeOutlined />} title="Streams">
            {
              // items array for this subMenu is from state
            }
            <MenuItem items={STREAM} />
          </SubMenu>
          <SubMenu key={'videos'} icon={<YoutubeOutlined />} title="Videos">
            <MenuItem items={VIDEO} />
          </SubMenu>
          <SubMenu key={'clips'} icon={<VideoCameraOutlined />} title="Clips">
            <MenuItem items={CLIP} />
          </SubMenu>
        </Menu>
      </Drawer>
    </>
  )
}

export default Favorites