import React from 'react';
import { Button, Card, List, message, Tabs, Tooltip } from 'antd';
import { StarOutlined, StarFilled } from '@ant-design/icons';
import { addFavoriteItem, deleteFavoriteItem } from '../utils';

// get library from ant design
const { TabPane } = Tabs;

const tabKeys = {
  Streams: 'stream',
  Videos: 'videos',
  Clips: 'clips',
}

// change URL from twitch to the format we need for our website
const processUrl = (url) => url
  .replace('%{height}', '252')
  .replace('%{width}', '480')
  .replace('{height}', '252')
  .replace('{width}', '480');

const renderCardTitle = (item, loggedIn, favs, favOnChange) => {
  const title = `${item.broadcaster_name} - ${item.title}`;

  // search in favs array(favorite items by user)
  // find is react method, search in favs array, if the item id has been favorited, give isFav true
  const isFav = favs.find((fav) => fav.id === item.id);

  // once click star/empty start button, will trigger fav on click fn here
  const favOnClick = () => {
    // if have been favorite, call deleteFavoriteItem to request detele this item in backend
    if (isFav) {
      deleteFavoriteItem(item).then(() => {
        // then update favorite item list (get data from backend again)
        favOnChange();
      }).catch(err => {
        message.error(err.message)
      })

      return;
    }

    // if have not been favorite, call addFavoriteItem to request add this item in backend
    addFavoriteItem(item).then(() => {
      // then update favorite item list (get data from backend again)
      favOnChange();
    }).catch(err => {
      message.error(err.message)
    })
  }

  return (
    <>
      {
        // if loged in 
        loggedIn &&
        
        <Tooltip title={isFav ? "Remove from favorite list" : "Add to favorite list"}>
          {
            // if the item have not been favorite, click will change icon to starFilled
            // if the item have nbeen favorite, click will change icon StarOutlined
            // and the click will triger favOnClick fn
          }
          <Button shape="circle" icon={isFav ? <StarFilled /> : <StarOutlined />} onClick={favOnClick} />
        </Tooltip>
      }
      <div style={{ overflow: 'hidden', textOverflow: 'ellipsis', width: 450 }}>
        <Tooltip title={title}>
          <span>{title}</span>
        </Tooltip>
      </div>
    </>
  )
}

// fn to render card grids 
const renderCardGrid = (data, loggedIn, favs, favOnChange) => {
  return (
    <List
      // different screen size will limit how many of grid to display
      grid={{
        xs: 1,
        sm: 2,
        md: 4,
        lg: 4,
        xl: 6,
      }}
      // dataSource is a property of List(from ant design: dataSource is DataSource array for list)
      // here, we use data from backend to update this property
      dataSource={data}
      // we give renderItem a function (tell List how to take of each element in dataSource), from ant design: ustomize list item when using dataSource
      renderItem={item => (
        <List.Item style={{ marginRight: '20px' }}>
          {
           // and design card for each item
          }
          <Card
            // use renderCardTitle to render each item 
            title={renderCardTitle(item, loggedIn, favs, favOnChange)}
          >
            <a href={item.url} target="_blank" rel="noopener noreferrer">
              <img
                alt="Placeholder"
                src={processUrl(item.thumbnail_url)}
              />
            </a>
          </Card>
        </List.Item>
      )}
    />
  )
}

// Home component
// App provide states to Home component(show all the videos, streams, clips on the right side)
const Home = ({ resources, loggedIn, favoriteItems, favoriteOnChange }) => {
  // parse resources from backend to three arrays
  const { VIDEO, STREAM, CLIP } = resources;
  
  // construct favorite items for each array and put in key(VIDEO, STREAM, CLIP) and value(an array) pair
  const { VIDEO: favVideos, STREAM: favStreams, CLIP: favClips } = favoriteItems;

  return (
    <Tabs
      // From ant design: Tabs make it easy to switch between different views.
      // set default tab
      defaultActiveKey={tabKeys.Streams}
    >
      {
        // three taps under TabPane, call renderCardGrid to display each tab
      }
      <TabPane tab="Streams" key={tabKeys.Streams} style={{ height: '680px', overflow: 'auto' }} forceRender={true}>
        {renderCardGrid(STREAM, loggedIn, favStreams, favoriteOnChange)}
      </TabPane>
      <TabPane tab="Videos" key={tabKeys.Videos} style={{ height: '680px', overflow: 'auto' }} forceRender={true}>
        {renderCardGrid(VIDEO, loggedIn, favVideos, favoriteOnChange)}
      </TabPane>
      <TabPane tab="Clips" key={tabKeys.Clips} style={{ height: '680px', overflow: 'auto' }} forceRender={true}>
        {renderCardGrid(CLIP, loggedIn, favClips, favoriteOnChange)}
      </TabPane>
    </Tabs>
  );
}

export default Home;